import GoodFlex from '@/components/goodflex/goodflex.vue';
import LoadMore from '../../components/uni-load-more/uni-load-more.vue'
import { requestip } from '@/pages/api/api.js';
export default{
	components:{
		GoodFlex,
		LoadMore
	},
	data(){
		return{
			pages:1,
			data:[],
			flexblean:false,
			showloadMore:false,
			loadMoreText:"加载中..."
		}
	},
	onLoad:function(e){
		uni.setNavigationBarTitle({
			title:e.name
		})
		this.freeInit();
	},
	methods:{
		// 数据初始化
		freeInit(){
			uni.request({
				url: requestip()+'/thirdreturn/index/specialprice?page=1',
				method: 'GET',
				data: {},
				success: res => {
					console.log('free:',res.data);
					this.data=res.data.goods_search_response.goods_list;
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onReachBottom() {
		this.pages+=1;
		this.showloadMore=true;
		uni.request({
			url: requestip()+'/thirdreturn/index/specialprice?page='+this.pages,
			method: 'GET',
			data: {},
			success: res => {
				for(var i=0 ;i<res.data.goods_search_response.goods_list.length;i++){
					this.data.push(res.data.goods_search_response.goods_list[i]);
				}
				this.showloadMore=false;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
